// import request from '../utils/request'

export default {
  name: 'bookCourse',
  data () {
    return {
      allCourse: [],
      menu: [],
      stuMenu: [{
        name: '全部',
        link: '/bookCourse/all',
        filter: ''
      }, {
        name: '待审核',
        link: '/bookCourse/booking',
        icon: 'icon-kexuejishu-',
        filter: ''
      }, {
        name: '预约成功',
        link: '/bookCourse/success',
        icon: 'icon-kexuejishu-',
        filter: ''
      }, {
        name: '预约失败',
        link: '/bookCourse/fail',
        icon: 'icon-kexuejishu-',
        filter: ''
      }, {
        name: '已结束',
        link: '/bookCourse/end',
        icon: 'icon-kexuejishu-',
        filter: ''
      }],
      teaMenu: [{
        name: '全部',
        link: '/bookCourse/all',
        filter: ''
      }, {
        name: '待确认',
        link: '/bookCourse/booking',
        icon: 'icon-kexuejishu-',
        filter: ''
      }, {
        name: '预约成功',
        link: '/bookCourse/success',
        icon: 'icon-kexuejishu-',
        filter: ''
      }, {
        name: '预约失败',
        link: '/bookCourse/fail',
        icon: 'icon-kexuejishu-',
        filter: ''
      }, {
        name: '已结束',
        link: '/bookCourse/end',
        icon: 'icon-kexuejishu-',
        filter: ''
      }]
    }
  },
  computed: {
    userType () {
      return this.$store.state.user.userType
    },
    pageType () {
      return this.$route.fullPath
    },
    // 课程状态：0表示待预约，1表示已被预约，2表示为正在进行，3表示课程正常结束，4表示课程失效
    curCourseType () {
      let tmpCurCourseType = []
      if (this.$route.params.type === 'booking') {
        tmpCurCourseType = [0]
      } else if (this.$route.params.type === 'success') {
        tmpCurCourseType = [1]
      } else if (this.$route.params.type === 'fail') {
        tmpCurCourseType = [4]
      } else if (this.$route.params.type === 'end') {
        tmpCurCourseType = [3]
      } else {
        tmpCurCourseType = []
      }
      return tmpCurCourseType
    }
  },
  beforeCreate () {
    this.$store.commit('SET_RETURN_TEXT', '我的课程')
    this.$store.commit('SET_RETURN_URL', '/my')
  },
  mounted () {
    console.log(this.$route)
    this.getCourse()
    if (this.$store.state.user.userType === 'student') {
      this.menu = this.stuMenu
    } else {
      this.menu = this.teaMenu
    }
  },
  watch: {
    '$route' (to) {
      console.log('d')
      this.getCourse()
    }
  },
  methods: {
    goto (url) {
      this.$router.push({ path: url })
    },
    gotoAll () {
      this.$router.push({ path: '/bookCourse/all' })
    },
    getCourse () {
      let url = ''
      let req = {}
      if (this.$store.state.userType === 'teacher') {
        url = '/tea/findTeaHistoryCourse'
        req = {
          teaCode: 1, // 是 string  教师编号
          page: 1, //  否 int 分页的当前页
          pageSize: 1000 //
        }
      } else {
        url = '/stu/lookhistory'
        req = {
          page: 1, // 否 int 页数
          size: 1000, // 否 int 页面大小
          stucode: 1 //
        }
      }
      console.log(url, req)
      setTimeout(() => {
      // request({
      //   url: url,
      //   method: 'get',
      //   data: req
      // }).then(res => {
      //   console.log(res)
        this.allCourse = [{
          'courseId': 1,
          'courseName': '数学圆的概念',
          'courseDate': '2018-12-28 16:00:00',
          'courseStartTime': '2018-12-30 14:32:24',
          'courseEndTime': '2018-12-31 14:32:28',
          'courseStatus': 0,
          'cause': '',
          'courseInteractive': 0,
          'courseLocation': '计算所',
          'createTime': '2018-12-28 22:38:27',
          'updateTime': '2018-12-28 22:38:27',
          'teaBase': {
            'teaCode': '6ss',
            'teaName': '邓庆东',
            'teaSubject': '数学',
            'teaOpenid': '123fe',
            'teaNickname': '东东',
            'teaHeadimgurl': '',
            'createTime': '2018-12-28 22:38:22',
            'updateTime': '2018-12-28 23:11:11'
          }
        }, {
          'courseId': 2,
          'courseName': '体育老师的关怀',
          'courseDate': '2018-12-30 16:00:00',
          'courseStartTime': '2019-01-01 15:09:21',
          'courseEndTime': '2019-01-02 15:09:26',
          'courseStatus': 0,
          'cause': '',
          'courseInteractive': 1,
          'courseLocation': '计算所',
          'createTime': '2018-12-28 23:12:08',
          'updateTime': '2018-12-28 23:12:08',
          'teaBase': {
            'teaCode': '888',
            'teaName': '孙鑫栋',
            'teaSubject': '体育',
            'teaOpenid': 'dfsaf',
            'teaNickname': '栋栋',
            'teaHeadimgurl': null,
            'createTime': '2018-12-28 23:10:30',
            'updateTime': '2018-12-28 23:24:56'
          }
        }].filter(item => {
          if (this.curCourseType.length === []) {
            return true
          } else {
            return this.curCourseType.indexOf(item.courseStatus) !== -1
          }
          // return item.courseStatus === this.curCourseType
        })
      }, 0)
    },
    cancelCourse (item) {
      this.$store.commit('SET_COURSE_NAME', item.courseName)
      this.$store.commit('SET_COURSE_ID', item.courseId)

      this.$store.commit('SET_RETURN_TEXT', '返回课程列表')
      this.$store.commit('SET_RETURN_URL', this.$route.path)

      this.$router.push({ name: 'cancelCourse' })
    },
    viewStu (item) {
      this.$store.commit('SET_COURSE_NAME', item.courseName)
      this.$store.commit('SET_COURSE_ID', item.courseId)

      this.$store.commit('SET_RETURN_TEXT', '返回课程列表')
      this.$store.commit('SET_RETURN_URL', this.$route.path)

      this.$router.push({ name: 'viewCourseStu' })
    }
  }
}



